.container{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    height: 94vh;
    margin: 1vh 1vw;
    padding: 2vh 1vw;
    background-color: #e1e1e1;
    border-radius: 10px;
}

.file-viewer{
    flex: 1;
    width: 40%;
    padding: 10px;
    overflow-y: auto;
    height: 88vh;
    margin: 1vh 0.5vw;
    border-radius: 10px;
    background-color: #ffffff;
}

.chatbot-container{
    display:flex;
    width: 40%;
}
@media(max-width : 768px){
    .container{
        height: 92vh;
        padding: 0;
        margin: 0;
    }
    .file-viewer{
        display: none;
    }
    .chatbot-container{
        width: 95%;
    }
}
